<template>
    <div class="logoin-main" ref="con">
        <div v-title data-title="ShopsN">{{title}}</div>
        <header class="logoIn-header">
           <span @click="remove" class="btn-back"></span>{{title}}
        </header>
        <div class="from">
            <div class="input-main phone-number">
                <span class="icon"></span>
                <input type="text" placeholder="" @blur="hid" @focus="hidden" v-model="mobile">
            </div>
            <div class="input-main message">
                <span class="icon"></span>
                <input type="text" placeholder=""  @blur="hid" @focus="hidden" v-model="message">
                <button class="btn-ver" :class="{active:isActive}" @click="obtain">{{btnText}}</button>
            </div>
            <button class="btn-in" @click="logoIn">&nbsp;&nbsp;</button>
        </div>
        <ul class="fail-link clearfix">
            <li class="fl" @click="toAcclogoIn"></li>
            <li class="fl" @click="goNow"> ></li>
        </ul>
        <dl class="tplogin" v-show="show">
            <dt>
                
                <span></span><span></span>
            </dt>
            <dd></dd>
            <dd></dd>
            <dd></dd>
        </dl>
    </div>
</template>
<script>
    import qs from 'qs';
    import {Toast} from 'mint-ui';
    export default {
        name : 'logoIn',
        data(){
            return {
                title:'ShopsN',
                show:true,
                userName:'',
                passWord:'',
                isActive:false,
                message:'',
                btnText:'',
                scrollWatch:true,
                mobile:''
            }
        },
        components:{
            
        },
        mounted() {
            let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
            this.$refs.con.style.height = h + 'px';
            document.body.scrollTop = 0;
        },
        destroyed(){
            this.scrollWatch = false;
        },
        methods:{
            hidden:function(){
                this.show = false;
            },
            hid:function(){
                this.show = true;
            },
            obtain : function(){
                if(!(/^1[34578]\d{9}$/.test(this.mobile))){
                    Toast('');
                    return;
                }
                if(this.isActive == true)return;
                this.isActive = true;
                var N = 60,
                    _this = this,
                    clear = null;
                this.axios.post(API_URL + 'Home/Register/short_message_send',qs.stringify({
                    mobile:this.mobile
                })).then((res) => {
                    Toast(res.data.msg);
                }).catch((err) => {
                    console.log(err);
                });
                this.btnText = ''+ N +'';
                this.isActive = true;
                clear = setInterval(function(){
                    _this.btnText = ''+ N-- +'';
                    if(N < 0){
                        clearInterval(clear);
                         _this.btnText = '';
                         _this.isActive = false;
                    }
                },1000);
            },
            remove:function(){//
                this.$router.go(-1)
            },
            logoIn:function(){//
                if(this.mobile == ''){
                    Toast({
                        message: '',
                        position: 'bottom',
                        duration: 1000
                    });
                    return;
                };
                if(this.message == ''){
                    Toast({
                        message: '',
                        position: 'bottom',
                        duration: 1000
                    });
                    return;
                };
                this.axios.post(API_URL + 'Home/Register/short_login',qs.stringify({
                    mobile:this.mobile,
                    verify:this.message
                })).then((res) => {
                    Toast(res.data.msg);
                    if(res.data.status == 1){
                        sessionStorage.setItem('user_ID', res.data.data.app_user_id);
                        this.$router.push('/home');
                        this.show = false;
                    }
                }).catch((err) => {
                    console.log(err);
                })
                this.show = false;
            },
            goNow:function(){//
                this.$router.push({
                    path : '/Register'
                });
            },
            toAcclogoIn:function(){
                this.$router.push({
                    path : '/LogoIn'
                });
                this.show = false;
            }
        }
    }
</script>
<style lang="less" scoped>
    .logoin-main{
        padding:0 .6rem;
        background:#fff;
        .logoIn-header{
            width:100%;
            height:.9rem;
            line-height:.9rem;
            text-align:center;
            font-size:.36rem;
            color:#333;
            position:relative;
            .btn-back{
                width:1.3rem;
                height:100%;
                font-size:.28rem;
                color:#999;
                position:absolute;
                left:-.6rem;
                top:0;
            }
        }
        .logo{
            width:100%;
            height:2.26rem;
            position:relative;
            img{
                width:3.4rem;
                height:.95rem;
                position:absolute;
                left:0;
                bottom:0;
                right:0;
                top:0;
                margin:auto;
            }
        }
        .from{
            padding-top:.8rem;
            .input-main{
                height:1.1rem;
                margin-bottom:.3rem;
                position:relative;
                .icon{
                    width:1.1rem;
                    height:100%;
                    position:absolute;
                    left:0;
                    top:0;
                }
                input{
                    width:100%;
                    height:100%;
                    border:none;
                    background:#f5f5f5;
                    text-indent:1.1rem;
                    border-radius:5px;
                    font-size:.28rem;
                }
            }
            .phone-number{
                .icon{
                    background:url(../../assets/phone.png) no-repeat center;
                    background-size:.36rem .5rem;
                }
            }
            .message{
                .icon{
                    background:url(../../assets/message.png) no-repeat center;
                    background-size:.41rem .33rem;
                }
                .btn-ver{
                    width:1.88rem;
                    height:1.04rem;
                    position:absolute;
                    right:.04rem;
                    top:50%;
                    margin-top:-.52rem;
                    border:none;
                    background:#fff;
                    font-size:.26rem;
                    color:#333;
                    border-radius:5px;
                    outline:none;
                }
                .btn-ver:active{
                    background:#f9781e;
                    color:#fff;
                }
                .btn-ver.active{
                    background:#f9781e;
                    color:#fff;
                }
            }
        }
        .btn-in{
            width:100%;
            height:1.1rem;
            border:none;
            border-radius:20px;
            outline:none;
            margin-top:.3rem;
            background: -webkit-linear-gradient(left, #f97522, #fd5148);
            background: -o-linear-gradient(right, #f97522, #fd5148);
            background: -moz-linear-gradient(right, #f97522, #fd5148);
            background: linear-gradient(to right, #f97522, #fd5148);
            font-size:.36rem;
            color:#fff;
        }
        .fail-link{
            padding-top:.3rem;
            li{
                width:50%;
                text-align:center;
                font-size:.28rem;
            }
            li:nth-child(1){
                border-right:1px solid #dedede;
                box-sizing:border-box;
                -moz-box-sizing:border-box;
                -webkit-box-sizing:border-box;
                color:#797979;
            }
            li:nth-child(2){
                color:#f9781e;
            }
        }
        .tplogin{
            width:6.3rem;
            position:absolute;
            left:0;
            bottom:0;
            padding:0 .6rem;
            text-align:center;
            dt{
                width:100%;
                font-size:.24rem;
                color:#9f9f9f;
                text-align:center;
                height:.5rem;
                line-height:.5rem;
                position:relative;
                span{
                    position:absolute;
                    top:50%;
                    width:2rem;
                    height:1px;
                    background:#ccc;
                }
                span:nth-child(1){
                    left:0;
                }
                span:nth-child(2){
                    right:0;
                }
            }
            dd{
                display:inline-block;
                width:1.21rem;
                height:1.21rem;
                background:url(../../assets/logoIn-qq.jpg) no-repeat;
                background-size:100% 100%;
                margin:.3rem 0;
            }
            dd:nth-child(3){
                margin-left:1.2rem;
                margin-right:1.2rem;
                background:url(../../assets/logoIn-wx.jpg) no-repeat;
                background-size:100% 100%;
            }
            dd:nth-child(4){
                background:url(../../assets/logoIn-wb.jpg) no-repeat;
                background-size:100% 100%;
            }
        }
    }
</style>


